@use "variables";

html,
body {
  min-height: 100vh;
}

body {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: variables.$space;
}

.header,
.main,
.footer {
  width: 25rem;
  margin: 0 auto;

  @media (min-width: variables.$bp-medium) {
    margin-left: variables.$space;
  }

  @media (min-width: variables.$bp-large) {
    margin-left: auto;
  }
}
