@use "variables";

.brace {
  position: relative;
  width: auto;
  min-width: variables.$brace-size*1.75;
  margin: variables.$space calc(#{variables.$brace-size} - #{variables.$content-outset});
  padding-bottom: variables.$brace-size;

  background-color: transparent;
  background-image:
    radial-gradient(
      circle at 0 0,
      rgba(variables.$brace-color,0) calc(#{variables.$brace-size}*0.75 - 0.5px),
      currentColor calc(#{variables.$brace-size}*0.75 + 0.5px),
      currentColor calc(#{variables.$brace-size} - 0.5px),
      rgba(variables.$brace-color,0) calc(#{variables.$brace-size} + 0.5px)
    ),
    radial-gradient(
      circle at variables.$brace-size*1.75 0,
      rgba(variables.$brace-color,0) calc(#{variables.$brace-size}*0.75 - 0.5px),
      currentColor calc(#{variables.$brace-size}*0.75 + 0.5px),
      currentColor calc(#{variables.$brace-size} - 0.5px),
      rgba(variables.$brace-color,0) calc(#{variables.$brace-size} + 0.5px)
    );
  background-size: variables.$brace-size*1.75 variables.$brace-size;
  background-position: center bottom;
  background-repeat: no-repeat;
  border: none;

  color: variables.$brace-color;
  filter: drop-shadow(0 1px 1px rgba(variables.$color-grey, 0.15));
  overflow: visible;

  &:before,
  &:after {
    content: '';
    position: absolute;
    top: 100%;
    width: 50%;
    height: 100%;
    margin-top: -(variables.$brace-size)/4;
    border-top: variables.$brace-size/4 solid currentColor;
  }

  &:before {
    left: -(variables.$brace-size)*0.75;
    border-left: 1px solid transparent;
    border-top-left-radius: 20% variables.$brace-size*1.5;
  }

  &:after {
    right: -(variables.$brace-size) * 0.75;
    border-right: 1px solid transparent;
    border-top-right-radius: 20% variables.$brace-size*1.5;
  }

  &--inverse {
    transform: rotate(180deg);
  }
}
