@use "sass:color";
@use "sass:math";
@use "variables";

// in case I want to change names
$section: 'section';

.#{$section} {
  position: relative;
  margin: variables.$space/2 auto;
  color: variables.$color-primary;
  font-size: 16px;
  opacity: 0.8;
  transition: opacity variables.$transition;

  // section colours
  @for $i from 2 through 10 {
    $section-color: color.adjust(
      color.adjust(variables.$color-primary, $hue: math.percentage($i * $i * 0.01)),
      $saturation: -(math.percentage($i * 0.03))
    );

    &:nth-of-type(#{$i}) {
      color: $section-color;

      a:focus {
        color: $section-color;
      }
    }
  }

  &:hover,
  &:focus,
  &:focus-within {
    opacity: 1;
  }

  &__heading {
    &:after {
      content: "";
      display: block;
      width: 0;
      margin: 0 auto;
      border: 0px solid transparent;
      border-bottom-color: currentColor;
      transition: border-width variables.$transition variables.$transition;
    }

    .#{$section}:focus &:after,
    .#{$section}:focus-within &:after {
      border-width: variables.$space/2 variables.$space variables.$space;
      transition: border-width variables.$transition;
    }

    &:hover {
      cursor: pointer;
    }

    @media (min-width: variables.$bp-medium) {
      &:after {
        position: absolute;
        right: -2 * variables.$space;
        top: 50%;
        border-color: transparent;
        border-right-color: currentColor;
        transform: translateY(-50%);
      }

      .#{$section}:focus &:after,
      .#{$section}:focus-within &:after {
        border-width: variables.$space;
        transition: border-width variables.$transition;
      }
    }

    @media (min-width: variables.$bp-large) {
      :nth-of-type(2n) > &:after {
        right: auto;
        left: -2 * variables.$space;
        border-color: transparent;
        border-left-color: currentColor;
      }
    }
  }

  &__content {
    position: relative;
    max-height: 0;
    margin: 0 calc(50% - 50vw);
    padding: 0 variables.$space;
    width: 100vw;
    background-color: currentColor;
    text-align: center;
    transition: max-height variables.$transition-slow, margin-bottom variables.$transition-slow;
    overflow: hidden;

    .#{$section}:focus > &,
    .#{$section}:focus-within > & {
      max-height: 470px;
      margin-bottom: variables.$space;
    }

    > * {
      position: relative;
      color: variables.$color-white;
      max-width: variables.$content-width * 2;
      margin: variables.$space/2 auto;

      &:first-child {
        margin-top: 0;
        padding-top: variables.$space * 1.5;
      }

      &:last-child {
        margin-bottom: 0;
        padding-bottom: variables.$space * 1.5;
      }
    }

    @media (min-width: variables.$bp-medium) {
      position: absolute;
      top: 50%;
      left: calc(100% + #{variables.$space}* 2);
      width: calc((100vw - #{variables.$content-width} - #{variables.$space}* 5));
      margin: initial;
      padding: 0 variables.$space * 2;
      text-align: left;
      transform: translateY(-50%);

      .#{$section}:focus-within & {
        display: block;
      }

      > * {
        margin-left: 0;

        &:first-child {
          padding-top: variables.$space;
        }

        &:last-child {
          padding-bottom: variables.$space;
        }
      }

      &:after {
        content: "";
        display: block;
        position: absolute;
        top: 50%;
        left: calc(100% + #{variables.$space});
        width: 50px;
        height: 50px;
        border: 0px solid red;
        border-width: 0 variables.$space variables.$space;
        border-right-color: red;
        z-index: 100;
      }
    }

    @media (min-width: variables.$bp-large) {
      width: calc((100vw - #{variables.$content-width} - #{variables.$space}* 4) / 2);

      :nth-of-type(2n) > & {
        left: auto;
        right: calc(100% + #{variables.$space}* 2);
        text-align: right;

        > * {
          margin-right: 0;
          margin-left: auto;
        }
      }
    }
  }
}
